<template>
  <div class="container content">
    <h1>{{data.title}}</h1>
    <div class="info">
      <i class="fa fa-calendar-o"></i>
      {{data.time}}
      &nbsp;
      &nbsp;
      &nbsp;
      &nbsp;
      &nbsp;
      &nbsp;
      <i class="fa fa-pencil"></i>
      {{data.num}}
    </div>
    <div class="contents" v-html="data.content"></div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    data () {
      return {
        data: {}
      }
    },
    created () {
      let pageId = this.$route.query.id
      for (let d of this.q_list) {
        if (d.id === parseInt(pageId)) {
          this.data = d
        }
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
.content
  font-size: 12px
  h1
    padding: 20px 0
    color: #e95420
  .info
    padding: 15px 0
    border-top: 1px dashed #dddddd
    border-bottom: 1px dashed #dddddd
    color: #e95420
  .contents
    padding: 15px 0
    line-height: 2
</style>



