import Vue from 'vue'
import Router from 'vue-router'
import list from '@/components/list/list'
import details from '@/components/details/details'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'list',
      component: list
    },
    {
      path: '/details',
      name: 'details',
      component: details
    }
  ]
})



