export default {
  install (Vue, options) {
    Vue.prototype.q_list = [
      {
        id: 1,
        title: 'vue路由组件传参',
        time: '2018年02月27日',
        num: 686,
        description: '在组件中使用 $route 会使之与其对应路由形成高度耦合，从而使组件只能在某些特定的 URL 上使用，限制了其灵活性。',
        content: '使用 props 将组件和路由解耦：\n' +
        '\n' +
        '取代与 $route 的耦合\n' +
        '\n' +
        'const User = {\n' +
        '  template: \'<div>User {{ $route.params.id }}</div>\'\n' +
        '}\n' +
        'const router = new VueRouter({\n' +
        '  routes: [\n' +
        '    { path: \'/user/:id\', component: User }\n' +
        '  ]\n' +
        '})\n' +
        '通过 props 解耦\n' +
        '\n' +
        'const User = {\n' +
        '  props: [\'id\'],\n' +
        '  template: \'<div>User {{ id }}</div>\'\n' +
        '}\n' +
        'const router = new VueRouter({\n' +
        '  routes: [\n' +
        '    { path: \'/user/:id\', component: User, props: true },\n' +
        '\n' +
        '    // 对于包含命名视图的路由，你必须分别为每个命名视图添加 `props` 选项：\n' +
        '    {\n' +
        '      path: \'/user/:id\',\n' +
        '      components: { default: User, sidebar: Sidebar },\n' +
        '      props: { default: true, sidebar: false }\n' +
        '    }\n' +
        '  ]\n' +
        '})\n' +
        '这样你便可以在任何地方使用该组件，使得该组件更易于重用和测试。\n' +
        '\n' +
        '布尔模式\n' +
        '\n' +
        '如果 props 被设置为 true，route.params 将会被设置为组件属性。\n' +
        '\n' +
        '对象模式\n' +
        '\n' +
        '如果 props 是一个对象，它会被按原样设置为组件属性。当 props 是静态的时候有用。\n' +
        '\n' +
        'const router = new VueRouter({\n' +
        '  routes: [\n' +
        '    { path: \'/promotion/from-newsletter\', component: Promotion, props: { newsletterPopup: false } }\n' +
        '  ]\n' +
        '})\n' +
        '函数模式\n' +
        '\n' +
        '你可以创建一个函数返回 props。这样你便可以将参数转换成另一种类型，将静态值与基于路由的值结合等等。\n' +
        '\n' +
        'const router = new VueRouter({\n' +
        '  routes: [\n' +
        '    { path: \'/search\', component: SearchUser, props: (route) => ({ query: route.query.q }) }\n' +
        '  ]\n' +
        '})\n' +
        'URL /search?q=vue 会将 {query: \'vue\'} 作为属性传递给 SearchUser 组件。\n' +
        '\n' +
        '请尽可能保持 props 函数为无状态的，因为它只会在路由发生变化时起作用。如果你需要状态来定义 props，请使用包装组件，这样 Vue 才可以对状态变化做出反应。\n' +
        '\n' +
        '更多高级用法，请查看例子。'
      },
      {
        id: 2,
        title: 'vue对组件注入',
        time: '2018年02月28日',
        num: 186,
        description: '通过在 Vue 根实例的 router 配置传入 router 实例，下面这些属性成员会被注入到每个子组件。',
        content: '注入的属性\n' +
        '\n' +
        '通过在 Vue 根实例的 router 配置传入 router 实例，下面这些属性成员会被注入到每个子组件。\n' +
        '\n' +
        '$router\n' +
        '\n' +
        'router 实例。\n' +
        '\n' +
        '$route\n' +
        '\n' +
        '当前激活的路由信息对象。这个属性是只读的，里面的属性是 immutable（不可变） 的，不过你可以 watch（监测变化）它。\n' +
        '\n' +
        '允许的额外配置\n' +
        '\n' +
        'beforeRouteEnter\n' +
        'beforeRouteUpdate (在 2.2 加入)\n' +
        'beforeRouteLeave\n' +
        '\n' +
        '查看组件内的守卫。'
      }
    ]
  }
}



