<template>
  <div class="container list">
    <ul>
      <li v-for="item in list">
        <h3>
          <router-link :to="{ path: '/details', query: { id: item.id }}">{{item.title}}</router-link>
        </h3>
        <div class="info">
          <i class="fa fa-calendar-o"></i>
          {{item.time}}
        </div>
        <div class="description">
          {{item.description}}
          <router-link :to="{ path: '/details', query: { id: item.id }}">阅读全文</router-link>
        </div>
      </li>
    </ul>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    data () {
      return {
        list: this.q_list
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .list
    padding: 20px 15px
    li
      padding: 10px 0
      border-bottom: 1px dashed #dddddd
      font-size: 12px
      color: #333333
      &:last-child
        border-bottom: 0 none
      h3
        a
          color: #e95420
      .info
        padding: 5px
        color: #e95420
      .description
        padding: 5px 0
        line-height: 2
        a
          display: inline-block
          padding: 1px 3px
          border-radius: 2px
          background: #e95420
          color: #ffffff
          text-decoration: none
          font-size: 10px
</style>



